'use client';

import { useState } from 'react';

const pacifico = Pacifico({ subsets: ['latin'], weight: '400' });
const montserrat = Montserrat({ subsets: ['latin'], weight: '400', variable: '--font-montserrat' });

export async function ProductList() {
  const products = await db.product.findMany({
    include: { images: true },
  });

  if (products.length === 0) {
    return (
      <p className={`text-center text-gray-500 ${montserrat.className}`}>
        Нет доступных товаров
      </p>
    );
  }

  return (
    <div className={`${montserrat.variable} font-sans p-4`}>
      <h1
        className={`${pacifico.className} text-4xl text-center mb-8 text-transparent bg-clip-text bg-gradient-to-r from-purple-600 to-pink-600`}
      >
        Наши Товары
      </h1>

      <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
        {products.map((product) => (
          <ProductCard key={product.id} product={product} />
        ))}
      </div>
    </div>
  );
}

// Отдельный компонент карточки товара
function ProductCard({ product }: { product: any }) {
  const [currentSlide, setCurrentSlide] = useState(0);
  const isOutOfStock = product.stock === 0;
  const images = product.images.length > 0
    ? product.images
    : [{ url: product.imageUrl || '/placeholder-product.jpg' }];

  return (
    <Link
      href={`/product/${product.id}`}
      className={`
        rounded-xl border p-5 transition-all duration-300 block
        ${isOutOfStock
          ? "bg-gray-200 border-gray-300"
          : "border-gray-200 shadow-sm hover:shadow-lg hover:border-purple-200"
        }
      `}
    >
      <div className="relative w-full overflow-hidden rounded-lg bg-gray-50 aspect-[4/3]">
        <div
          className="flex transition-transform duration-300 ease-in-out"
          style={{ transform: `translateX(-${currentSlide * 100}%)` }}
        >
          {images.map((img: any, idx: number) => (
            <img
              key={idx}
              src={img.url}
              alt={product.name}
              className="w-full h-full object-cover shrink-0 grow-0 basis-full"
            />
          ))}
        </div>

        {images.length > 1 && (
          <>
            <button
              onClick={(e) => {
                e.preventDefault();
                setCurrentSlide((prev) => (prev - 1 + images.length) % images.length);
              }}
              className="absolute left-2 top-1/2 transform -translate-y-1/2 bg-white/80 px-2 py-1 rounded-full shadow"
            >
              ‹
            </button>
            <button
              onClick={(e) => {
                e.preventDefault();
                setCurrentSlide((prev) => (prev + 1) % images.length);
              }}
              className="absolute right-2 top-1/2 transform -translate-y-1/2 bg-white/80 px-2 py-1 rounded-full shadow"
            >
              ›
            </button>
          </>
        )}
      </div>

      <h2
        className={`text-xl font-bold line-clamp-2 ${
          isOutOfStock
            ? "text-gray-500"
            : "hover:text-purple-600 transition-colors font-serif"
        }`}
      >
        {product.name}
      </h2>

      <p className="text-sm text-gray-600 line-clamp-3 mb-3 mt-2 italic">
        {product.description}
      </p>

      {isOutOfStock ? (
        <p className="font-semibold text-red-500 mt-1">Нет в наличии</p>
      ) : (
        <p className={`font-bold text-purple-600 text-lg ${pacifico.className}`}>
          {product.price.toFixed(2)} ₽
        </p>
      )}
    </Link>
  );
}
